import styles from './NotFoundBlock.module.scss';
import pizza from './../../assets/img/pizza-error.jpg';

const NotFoundBlock = () => {
    return (
        <div className={styles.root}>
            <div className={styles.wrapperImg}><img className={styles.img} src={pizza} alt="" /></div>
            <h1>Ничего не найдено</h1>
            <p className={styles.sub_title}>К сожалению данная страница отсутствует в нашем магазине</p>
        </div>
    );
}

export default NotFoundBlock;